'use client';

import { useState } from 'react';
import { RemessaInfo } from '@/types/jadlog';
import UploadXML from '@/components/UploadXML';
import RemessasList from '@/components/RemessasList';

export default function Home() {
  const [remessas, setRemessas] = useState<RemessaInfo[]>([]);

  const handleUpload = (novasRemessas: RemessaInfo[]) => {
    setRemessas(prev => [...prev, ...novasRemessas]);
  };

  const handleClear = () => {
    setRemessas([]);
  };

  return (
    <main className="min-h-screen bg-gray-50 py-8">
      <div className="container mx-auto px-4">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">
            Rastreamento de Encomendas Jadlog
          </h1>
          <p className="text-lg text-gray-600 max-w-2xl mx-auto">
            Importe seus arquivos XML da Jadlog para visualizar as informações das remessas de forma organizada
          </p>
        </div>

        {/* Upload Section */}
        <div className="mb-12">
          <UploadXML onUpload={handleUpload} />
        </div>

        {/* Results Section */}
        <RemessasList remessas={remessas} onClear={handleClear} />

        {/* Instructions */}
        {remessas.length === 0 && (
          <div className="max-w-3xl mx-auto mt-16">
            <div className="bg-white rounded-lg shadow-sm p-8">
              <h3 className="text-xl font-semibold text-gray-900 mb-4">
                Como usar esta ferramenta
              </h3>
              <div className="space-y-4 text-gray-600">
                <div className="flex items-start space-x-3">
                  <div className="flex-shrink-0 w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium">
                    1
                  </div>
                  <p>
                    <strong>Selecione os arquivos XML:</strong> Clique no botão de upload acima e selecione um ou mais arquivos XML fornecidos pela Jadlog.
                  </p>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="flex-shrink-0 w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium">
                    2
                  </div>
                  <p>
                    <strong>Visualize as informações:</strong> Após o upload, você verá uma tabela com os dados extraídos: nome do destinatário, CPF, CNPJ do remetente e número operacional.
                  </p>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="flex-shrink-0 w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium">
                    3
                  </div>
                  <p>
                    <strong>Gerencie sua lista:</strong> Você pode adicionar mais arquivos ou limpar a lista a qualquer momento.
                  </p>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Footer */}
        <footer className="mt-16 text-center text-gray-500 text-sm">
          <p>Ferramenta para uso pessoal - Processamento local dos arquivos XML</p>
        </footer>
      </div>
    </main>
  );
}
